.navbar {
  width: 100%;
  height: 62px;
  display: none;
  -ms-flex-pack: center;
      justify-content: center;
  color: #ffffff;
  z-index: 100;
  position: absolute;
  -webkit-animation: 0.7s ease-out 1 slideInFromTop;
          animation: 0.7s ease-out 1 slideInFromTop;
}
.nav-logo {
  width: 34px;
  margin-left: 16px;
  cursor: pointer;
}
.nav-title {
  margin: 0;
  font-weight: 300;
  cursor: pointer;
}
.nav-title:hover, .active {
  -webkit-text-decoration: underline #ff4d79;
          text-decoration: underline #ff4d79;
  text-underline-position: under;
}
.fixed-nav {
  position: fixed;
  background: #ffffff;
  color: #252429;
  -webkit-box-shadow: 2px 2px 2px #e6e6e6;
          box-shadow: 2px 2px 2px #e6e6e6;
}
.nav-contents {
  width: 100%;
  max-width: 1200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.nav-item {
  margin: 0 16px;
}
.mobile-nav {
  position: fixed;
}

@media only screen and (min-width: 768px) {
  .navbar {
    display: -ms-flexbox;
    display: flex;
  }
  .mobile-nav {
    display: none;
  }
}



