.Menu {
  height: 0;
  width: 100%;
  background: #1a1a1a;
  color: #ffffff;
  z-index: 100;
  position: fixed;
  overflow: hidden;
  -webkit-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}
.menu-icon {
  width: 20px;
  height: 20px;
  padding: 12px;
  margin: 16px;
  cursor: pointer;
  z-index: 100;
  position: absolute;
  border: 1px solid #ffffff;
  -webkit-border-image: -webkit-linear-gradient(#ff4d4d, #ff4d79, #ff4da6);
  -o-border-image: -o-linear-gradient(#ff4d4d, #ff4d79, #ff4da6);
     border-image: -webkit-gradient(linear, left top, left bottom, from(#ff4d4d), color-stop(#ff4d79), to(#ff4da6));
     border-image: linear-gradient(#ff4d4d, #ff4d79, #ff4da6);
  border-image-slice: 1;
  -webkit-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}
.dark {
  background: #ffffff;
  border-color: #595959;
}
.mobile-menu {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.show-menu {
  height: 100vh;
}
.menu-item-wrapper {
  height: 0px;
  overflow: hidden;
  -webkit-transition: all 0.3s 0.3s ease-in;
  -o-transition: all 0.3s 0.3s ease-in;
  transition: all 0.3s 0.3s ease-in;
}
.show-item {
  height: 72px;
}
.menu-item {
  margin: 0;
  font-size: 28px;
  font-weight: 300;
  text-align: center;
  cursor: pointer;
}
.menu-item:hover, .underline {
  -webkit-text-decoration: underline #ff4d79;
          text-decoration: underline #ff4d79;
  text-underline-position: under;
}

@media only screen and (min-width: 768px) {
  .Menu {
    display: none;
  }
}



