.App {
  font-weight: 300;
}
.app-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 72px 16px;
}
.app-section h1 {
  font-size: 40px;
  font-weight: 400;
  letter-spacing: 3px;
  text-align: center;
  margin-bottom: 16px;
  color: #252429;
}
.button {
  height: 48px;
  min-width: 110px;
  padding: 16px;
  margin: 8px;
  border-radius: 0;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
}
.button-border {
  border: solid 1px #252429;
  color: #252429;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.button-border:hover {
  font-weight: 300;
  background-color: #252429;
  color: #ffffff;
}
.center-vertical {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.center {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.accent {
  color: #ff4d79;
}
.gradient:hover {
  background: #ff4d4d;
  background: -webkit-gradient(linear, left top, left bottom, from(#ff4d4d), color-stop(#ff4d79), to(#ff4da6));
  background: -webkit-linear-gradient(#ff4d4d, #ff4d79, #ff4da6);
  background: -o-linear-gradient(#ff4d4d, #ff4d79, #ff4da6);
  background: linear-gradient(#ff4d4d, #ff4d79, #ff4da6);
}
.fade-in {
  -webkit-animation: 500ms ease-in 1 fadeIn;
          animation: 500ms ease-in 1 fadeIn;
}

@-webkit-keyframes slideInFromTop {
  0% {
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
  }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes slideInFromTop {
  0% {
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
  }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



